.intro {
  border-radius: 0px;
  overflow:visible;
  .intro-graphic-container {
    img {
      margin-top: -$space-2xl;
      border-radius: $border-radius-lg;
      @include breakpoint-md {
        border-radius: $border-radius-md;
      }
    }
  }
  .intro-content {
    padding-top: $space-sm;
    padding-bottom: $space-md;
    @include breakpoint-md {
      grid-gap:0px;
      padding-bottom: $space-lg;
      max-width:600px;
      text-align: center;
    }
    p {
      margin: 16px 0;
    }
  }
  .marquee-container {
    padding: 0 0 $space-xl 0;
    .overline-sm {
      color: $neutral-800;
      padding-bottom: $space-sm;
    }
    .marquee {
      position: relative;
      width: 100vw;
      max-width: 100%;
      height: 220px;
      overflow-x: hidden;
    }
    .marquee-track {
      position: absolute;
      white-space: nowrap;
      will-change: transform;
      animation: marquee 60s linear infinite;
      line-height:160px;
      .marquee-content {
        font-family: $family-display;
        font-weight: $font-black;
        font-size: 160px;
        line-height: $leading-sm;
        letter-spacing: $tracking-base;
        color: $neutral-900;
        opacity: $opacity-80;
        span {
          &:before {
            content: "";
            display: inline-block;
            background: $neutral-900;
            border-radius: $border-radius-xl;
            width: 40px;
            height:40px;
            margin: 24px 24px;
          }
        }
      }
    }
    @include breakpoint-md {
      padding: 0 0 $space-md 0;
      .marquee {
        height: 140px;
      }
      .marquee-track {
        line-height:80px;
        .marquee-content {
          font-size: 80px;
          span {
            &:before {
              width: 20px;
              height:20px;
              margin: 12px 16px;
            }
          }
        }
      }
    }
    @keyframes marquee {
      from { transform: translateX(0); }
      to { transform: translateX(-50%); }
    }
  }
}